import { Injectable } from '@angular/core';
import * as moment from 'moment'
@Injectable({
  providedIn: 'root'
})
export class FounderInfoService {

  public joey: Array<string> = [
    'Age: ' + this.getAge1994(),
    'I came up with the idea for Spotback when I spent 45 minutes searching for parking. I decided how the app would work, made the design and made the co-founding team to bring the app to life',
  ];
  public shamaun: Array<string> = [
    'Age: ' + this.getAge1994(),
    'I am a curious observer and proactive learner, I believe in analyzing problems in a way that generates insights and solutions which are remarkable in their simplicity and efficiency. Contact : Shamaun14@gmail.com , 669 234 2870',
  ];
  public dmitry: Array<string> = [
    'Age: ' + this.getAge1996(),
    'I am an ios developer with a history of success, I made a photo editing app which was downloaded over 2 million times.' +
    ' For spotback, I built the entire ios part of the application.',
  ];
  public dylan: Array<string> = [
    'Age: ' + this.getAge1995(),
    'I am a Full-stack Web Developer specializing in backend development.' +
    ' My contributions to spotback, include designing, building and deploying the backend services and matching service that power the spotback application, as well as this website.',
  ];


  public getAge1996() {
    var age = Number.parseInt(moment(new Date()).format('YYYY')) - 1996;
    return age;
  }

  public getAge1995() {
    var age = Number.parseInt(moment(new Date()).format('YYYY')) - 1995;
    return age;
  }
  public getAge1994() {
    return Number.parseInt(moment(new Date()).format('YYYY')) - 1994;
  }
  public getShamaun() {
    return this.shamaun;
  }
  public getDmitry() {
    return this.dmitry;
  }
  public getDylan() {
    return this.dylan;
  }
  public getJoey() {
    return this.joey;
  }

  constructor() { }
}
