import { Component, OnInit } from '@angular/core';
import { GeneralDataService } from 'src/app/services/general-data.service';
import {Router, ActivatedRoute} from '@angular/router';
import {FormsModule} from '@angular/forms';


@Component({
  selector: 'app-get-early-access',
  templateUrl: './get-early-access.component.html',
  styleUrls: ['./get-early-access.component.css']
})
export class GetEarlyAccessComponent implements OnInit {
  public email:string;

  constructor(public generalDataService: GeneralDataService, private router: Router, private aroute: ActivatedRoute) { }

  ngOnInit() {
  }

  signUp() {
    class count {
      count: number;
    };
    if(!this.email) {
      return;
    } else {
      let param1: string;
      this.aroute.queryParams.subscribe(params => {
        param1 = params['email'];
        console.log(param1);
        console.log(this.email);
        this.generalDataService.signUp(this.email, param1).subscribe((data: count) => {
            console.log(":" + data.count);
            this.generalDataService.count = data.count;
            this.router.navigate(['/sign-up']);
        });
      });
        
    }
  }

}