import { Component, OnInit } from '@angular/core';
import { FounderInfoService } from '../../services/founder-info.service';
import { RouterModule, Router } from '../../../../node_modules/@angular/router';
import { Founder } from '../../models/founder';
import { EmbedVideoService } from 'ngx-embed-video';
@Component({
  selector: 'app-company',
  templateUrl: './company.component.html',
  styleUrls: ['./company.component.css']
})
export class CompanyComponent implements OnInit {
  public currentFounder;
  public showFounder: boolean;
  public contentEditable: boolean;
  
  public founder: string[] = [];
  
  public joey = this.founderInfo.getJoey();
  public shamaun = this.founderInfo.getShamaun();
  public dmitry = this.founderInfo.getDmitry();
  
  public dylan = this.founderInfo.getDylan();

  public usersIngredients: [] = [];

  categoryColor: string;

  constructor(router: Router, private founderInfo: FounderInfoService, private embedService: EmbedVideoService) { }

  ngOnInit() {
    this.showFounder
 = false;

  }
  


  public sortFounder() {
    this.founder.sort(function (a, b) {
      if (a < b) return -1;
      if (a > b) return 1;
      return 0;
    });
  
}

  public setCategory = (category) => {
  if (this.currentFounder == category) return;
  this.currentFounder = category;
}

  public showJoey() {
  this.founder = this.joey;
  this.sortFounder();
  this.currentFounder = 'Joey Caruana Co-founder';
  this.showFounder = true;
}

  public showShamaun() {
  this.founder = this.shamaun;
  this.sortFounder();
  this.currentFounder = "Shamaun Anees Co-founder";
  this.showFounder = true;
}

  public showDylan() {
  this.founder = this.dylan;
  this.sortFounder();
  this.currentFounder = "Dylan Corbus Co-founder";
  this.showFounder = true;

}

  public showDmitry() {
  this.founder = this.dmitry;
  this.sortFounder();
  this.currentFounder = "Dmitry Sokolov Co-founder";
  this.showFounder = true;
}



}

