import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';






@Injectable({
  providedIn: 'root'
})
export class SpotsService {
  apiURL: string = 'https://www.spotback.io/spots/Prod/spots?coordinates=';
  constructor(public httpClient: HttpClient) { }

  public getSpots(coordinates: string){
    class spots {
      coordinates: string;
    };
    class result {
      spots: spots[]
      coordinates: string;
    };
    return this.httpClient.get<result>(`${this.apiURL}${coordinates}`);
  }
}
