import { NgModule } from '@angular/core';
import { Routes, RouterModule, UrlSegment } from '@angular/router';
import { MenuComponent } from './components/menu/menu.component'
import { HomeComponent } from './components/home/home.component'
import { AboutComponent } from './components/about/about.component'
import { CompanyComponent } from './components/company/company.component'
import { CitesComponent } from './components/cites/cites.component'
import { GetEarlyAccessComponent } from './components/get-early-access/get-early-access.component';
import { SignUpComponent } from './components/sign-up/sign-up.component';

const routes: Routes = [
  { path: '', redirectTo: 'waitlist', pathMatch: 'full'},
  { path:'about', redirectTo: 'about', pathMatch: 'full'},
  { path:'sign-up', redirectTo: 'sign-up', pathMatch: 'full'},
  { path:'home', redirectTo: 'home', pathMatch: 'full'},
  { path:'company', redirectTo: 'company', pathMatch: 'full'},
  { path:'cities', redirectTo: 'cities', pathMatch: 'full'},
  { path: 'home', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'company', component: CompanyComponent },
  { path: 'cities', component: CitesComponent },
  {
    component: GetEarlyAccessComponent,
    matcher: (url: UrlSegment[]) => {
      return url.length === 1 && url[0].path.indexOf('waitlist?email=') > -1 ? ({consumed: url}) : null;
    }
  },
  { path:'waitlist', redirectTo: 'waitlist', pathMatch: 'full'},
  { path: 'waitlist', component: GetEarlyAccessComponent },
  { path: 'sign-up', component: SignUpComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
