import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { stringify } from 'querystring';

@Injectable({
  providedIn: 'root'
})
export class GeneralDataService {
  apiURL: string = 'https://www.spotback.io/support/waitlist/signUp';
  public count: number;
  public email: string;
  constructor(public httpClient: HttpClient) { }

  public signUp(id: string, referral: string){
    this.email = id;
    let request = {};
    if(referral) {
      request['referrer'] = referral
      request['email'] = this.email;
    } else {
      request['email'] = this.email;
    }

    class count {
      count: number;
    };
    console.log(JSON.stringify(request));
    return this.httpClient.post<count>(this.apiURL, JSON.stringify(request));
  }
}
